.package-view-section {
	width: 100%;
	padding: 20px get-rem(47px) 20px; }

.package-view-wrap {
	max-width: 1058px;
	margin: 0 auto;
	.owl-carousel {
		.owl-nav {
			button {
				&.owl-prev, &.owl-next {
					background-color: #fff;
					width: get-rem(47px);
					height: get-rem(47px);
					color: #000;
					line-height: get-rem(47px);
					border-radius: 10em;
					position: absolute;
					top: 247px;
					z-index: 10;
					transition: all .3s ease;
					&:active, &:hover {
						background-color: $accent;
						color: #fff; } }

				&.owl-prev {
					left: get-rem(-94px);
					display: inline-block; }
				&.owl-next {
					right: get-rem(-94px); } } }
		.owl-dots {
			text-align: center;
			height: 10px; }
		.owl-dot {
			width: 9px;
			height: 9px;
			background-color: $accent;
			margin: 0 5px;
			border-radius: 10em;
			transition: all .4s ease;
			&.active {
				width: 38px; } } } }


.package-view {
	width: 100%;
	position: relative;
	padding: 9% 0 4.2vw;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background-size: contain;
	background-position: top center;
	background-repeat: no-repeat;
	&__container {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		color: #000;
		font-size: get-rem(20px);
		&:nth-child(2) {
			flex-direction: row;
			justify-content: space-around;
			margin-top: 26.5%;
			align-items: center; } }
	&__title {
		font-weight: 700;
		font-size: 5.2vw;
		border-bottom: 1px solid $grey-text;
		line-height: 1.5; }
	&__sub-title {
			font-size: get-rem(30px);
			font-weight: 700;
			margin: 0; }
	&__box {
		flex-grow: 1;
		max-width: 410px; }
	&__cta {
		width: 173px; }

	&__btn {
		margin: 17px auto 0; } }

@media only screen and (max-width : 1560px) {
	/**/
	.package-view {
		&-wrap {
			max-width: 907.2px; } } }

@media only screen and (max-width : 1200px) {
	/**/
	.package-view {
		&-wrap {
			.owl-carousel {
				.owl-nav {
					button {
						&.owl-prev, &.owl-next {
							top: 180px; }
						&.owl-prev {
							left: get-rem(-47px); }

						&.owl-next {
							right: get-rem(-47px); } } } } } } }



@media only screen and (max-width : 992px) {
	/**/
	.package-view {
		&__container {
			&:nth-child(2) {
				margin-top: 30%;
				flex-direction: column; } } } }


@media only screen and (max-width : 768px) {
	/**/
	.package-view {
		padding-top: 0;
		&-section {
			padding: 0 20px; }

		&__title {
			font-size: 35px; } } }

@media only screen and (max-width : 480px) {
	.package-view {
		background-position: -10px 64px;
		&__title {
			align-items: center; }
		&__container:nth-child(2) {
			margin-top: 44%; } } }
