#offer__page {
	.block-content {
		margin: 0;
		max-width: 100%; } }


.offer-cart {
	display: flex;
	justify-content: space-between;
	@include m(768) {
		flex-wrap: wrap; } }

.cart {
	&-gallery {
		flex-grow: 1;
		flex-basis: 696px;
		flex-shrink: 1;
		min-width: 221px;
		max-width: 750px;
		&-valuation {
			display: flex;
			justify-content: space-between;
			&__btn {
				font-size: 100px;
				background-image: radial-gradient($light-grey, white);
				transition: all .4s ease;
				&:hover, &:active {
					.fas {
						color: lighten($accent, 5%); } }
				&:active {
					.fas {
						color: darken($accent, 5%); } } } }

		&-carousel {
			display: flex;
			&__item {
				padding: 32% 0;
				background-size: contain;
				background-repeat: no-repeat;
				background-position: center; }
			&.owl-carousel {
				.owl-item {
					padding: 0 10px;
					margin: {
						top: 0;
						bottom: 0; } }
				.owl-dots {
					text-align: center;
					height: 10px;
					position: relative;
					top: 12rem;
					@include m(480) {
						top: 9rem; } }
				.owl-dot {
					width: 9px;
					height: 9px;
					background-color: $accent;
					margin: 0 5px;
					border-radius: 10em;
					transition: all .4s ease;
					&.active {
						width: 38px; } }

				.owl-nav {
					button {
						&.owl-next, &.owl-prev {
							display: inline-block;
							top: 50%;
							background-color: #fff;
							width: 2.7rem;
							height: 2.7rem;
							color: #000;
							line-height: 2.7rem;
							border-radius: 10em;
							position: absolute;
							z-index: 10;
							transition: all .3s ease;
							&:active, &:hover {
								background-color: $accent;
								color: #fff; }
							.fas {
								font-size: 1.08rem; } }

						&.owl-next {
							right: 0; }
						&.owl-prev {
							left: 0; } } } } }

		.owl-thumbs {
			width: 100%;
			display: flex;
			justify-content: space-between;
			margin: 1rem 0 5.65rem; }
		.owl-thumb-item {
			width: 25%;
			padding: 8% 0;
			opacity: .5;
			transition: all .4s ease;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center;
			@include m(768) {
				transform: scale(1.5); }

			&:nth-child(2) {
				margin: 0 -10px 0; }

			&.active {
				transform: scale(1.7);
				opacity: 1; } } }

	&-info {
		flex-basis: 522px;
		min-width: 220px;
		margin-left: 5%;
		flex-shrink: 2;
		@include m(768) {
			margin: 3rem auto; }

		&__title {
			font-size: 3.4vw;
			line-height: 1;
			color: #000;
			font-weight: 700;
			margin-bottom: 1.5rem;
			@include m(992) {
				font-size: 29px; } }

		&__composition {
			padding-bottom: 2.7vw;
			border-bottom: 1px solid black;
			max-width: 409px; }
		&__cta {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			max-width: 409px;
			min-width: 221px;
			margin-top: 5.57vw;
			color: #000; }

		&__price, &__basket {
			font-size: 1.765rem;
			font-weight: 700;
			&.fas {
				font-size: 1.765rem;
				font-weight: 700;
				line-height: inherit; } }

		&-colors {
			width: 100%;
			display: flex;
			justify-content: space-between;
			margin-top: get-rem(61px);
			align-items: center;
			&__circle {
				max-width: get-rem(56px);
				max-height: get-rem(56px);
				width: 14%;
				padding: 7%;
				border-radius: 10em;
				transition: all .4s ease;
				&.is-active {
					transition: all .4s ease;
					transform: scale(1.6); } } } } }



$colors: $yellow, $grey, $blue, $rose, $violet;

@for $i from 1 through length($colors) {

	.cart-info-colors__circle:nth-child(#{length($colors)}n+#{$i}) {
		background-color: nth($colors, $i); } }
