//for header btns_________________________

%btn {
	position: relative;
	white-space: nowrap;
	cursor: pointer;
	outline: none;
	border: none;
	text-align: center;
	vertical-align: top;
	transition: all .4s ease;
	border-radius: 10em;
	height: 2.53rem;
	font-size: .88rem;
	font-family: $default-font;
	transition: all .2s ease;
	@include m(1024) {
		transition: all .4s ease; } }

.btn-pad {
	@extend %btn;
	display: inline-block;
	padding: 0 1rem;
	line-height: 2.53rem;
	.fas {
		font-size: 1.18rem;
		line-height: inherit; } }

//_____________

%btn-base {
	position: relative;
	white-space: nowrap;
	cursor: pointer;
	outline: none;
	border: none;
	text-align: center;
	vertical-align: top;
	transition: all .4s ease;
	border-radius: 10em;
	height: get-rem(48.8px);
	min-height: 28px;
	background-color: $accent;
	color: #fff;
	font-weight: 700;
	font-size: 1.17em;
	font-family: $default-font;
	transition: all .2s ease;
	@include min(1024) {
		transition: all .4s ease; }
	&:focus, &:hover {
		@include min(1024) {
			text-decoration: none;
			color: #fff; } }
	&:hover {
		@include min(1024) {
			background-color: lighten($accent, 20%); } }

	&:active {
		background-color: darken($accent, 20%);
		outline: none; } }

.btn-wide {
	@extend	%btn-base;
	width: 100%; }


.btn {
	@extend %btn-base;
	width: 9vw;
	min-width: 90px;

	//to do: refactor code to delete (.button.grey)
	&-accent {
		color: #fff;
		text-decoration: none;
		background-color: $accent;
		&:focus, &:hover {
			text-decoration: none;
			color: #fff;
			.fas {
				color: #fff; }
			.icon, .icon-item {
				fill: #fff; } }

		&:hover {
			background-color: lighten($accent, 5%); }

		&:active {
			background-color: darken($accent, 5%);
			outline: none;
			.icon, .icon-item {
				fill: #fff; } } }
	&-transp {
		color: #000;
		background-color: none;
		&:focus, &:hover {
			@include min(1024) {
				text-decoration: none;
				color: #fff; } }
		&:hover {
			@include min(1024) {
				background-color: lighten($accent, 5%); } }

		&:active {
			color: #fff;
			background-color: darken($accent, 5%);
			outline: none; } }

	&-grey {
		background-color: $light-grey;
		transition: all .2s ease;

		@include min(1024) {
			transition: all .4s ease; }
		&:focus, &:hover {
			@include min(1024) {
				text-decoration: none;
				color: #fff;
				.fas {
					color: #fff; }
				.icon, .icon-item {
					fill: #fff; } } }
		&:hover {
			@include min(1024) {
				background-color: lighten($accent, 10%); } }

		&:active {
			text-decoration: none;
			color: #fff;
			background-color: darken($accent, 12%);
			outline: none;
			.fas {
				color: #fff; }
			.icon, .icon-item {
				fill: #fff; } } } }


.button {
	display: inline-block;
	border: none;
	color: #fff;
	text-decoration: none;
	background-color: $accent;
	padding: 11px 16.7px;
	font-size: 15px;
	font-weight: 400;
	border-radius: 10em;
	text-align: center;
	position: relative;
	cursor: pointer;
	outline: none;
	line-height: 1.5;
	white-space: nowrap;
	vertical-align: top;
	transition: background-color .2s ease;

	@include min(1024) {
		transition: background-color .3s ease; }

	&.transparent {
		background-color: inherit;
		color: #000; }
	&.grey {
		background-color: $light-grey;
		color: #000; }

	&.fa {
		padding: {
			top: 10px;
			bottom: 10px; } }

	&:focus, &:hover {
		@include min(1024) {
			text-decoration: none;
			color: #fff; } }
	&:hover {
		@include min(1024) {
			background-color: lighten($accent, 5%); } }

	&:active {
		color: #fff;
		background-color: darken($accent, 5%);
		outline: none; }

	&.square {
		border: 2px solid $accent;
		border-radius: 10px;
		color: $accent;
		background-color: inherit;
		width: 100%;
		height: $square-height;
		line-height: $square-height;
		padding: 0 28px;
		font: bold 25px $default-font;
		transition: all .4s ease;
		&:hover {
			color: #fff;
			background-color: lighten($accent, 5%); }
		&:active {
			color: #fff;
			background-color: darken($accent, 5%);
			outline: none; }
		@include m(1560) {
			font-size: 1.29rem; }
		@include m(992) {
			height: $square-height-mid;
			line-height: $square-height-mid; }
		&.overlay {
			color: #fff;
			background-color: $accent;
			&:hover {
				background-color: $bgc;
				color: $accent; }
			&:active {
				color: $accent;
				background-color: $bgc;
				outline: none; } } }

	&-soc {
		display: inline-block;
		height: 69px;
		width: 26.43%;
		border-radius: 10px;
		color: #fff;
		line-height: 69px;
		font-size: 24px;
		cursor: pointer;
		font-family: $default-font;
		&:nth-child(1) {
			background-color: #1876D2; }
		&:nth-child(2) {
			background-color: #EF4B39; }
		&:nth-child(3) {
			background-color: #00A4E4; }
		@include m(1560) {
			font-size: 1.29rem;
			width: 30%; }
		@include m(992) {
			height: $square-height-mid;
			line-height: $square-height-mid; }
		.fab {
			margin-right: 15px; }

		&:hover {
			background-color: lighten($accent, 5%); }

		&:active {
			background-color: darken($accent, 5%);
			outline: none; } } }


a.button.square {
	line-height: 4.05882rem;
	@include m(1560) {
		padding-top: 0; }
	@include m(992) {
		line-height: $square-height-mid; } }

.social-btn-composition {
	display: flex;
	justify-content: space-between; }


.rating-hand-btn {
	border-radius: 10em;
	.fas {
		color: orange;
		font-size: 1.765rem;
		padding: 1.24rem 2.058rem;
		line-height: 1;
		transition: all .2s ease; } }
