#nike-bot__page {

	.h2 {
		margin-bottom: 48px; }

	.square-input, .button.square {
		border-radius: 10em;
		height: 56px;
		line-height: 56px;
		border-width: 1px; } }

#search__form {
	.square-input, .button.square {
		border-radius: 10em;
		height: 56px;
		line-height: 56px;
		border-width: 1px; } }


.nike-bot__section {
	padding: 0 21.6%;
	margin-bottom: 70px; }

.emoji {
	display: flex;
	justify-content: center;
	margin: 156px 0 106px;
	.img-responsive {
		width: 213px;
		margin: 0 34px; } }

#search__form {
	position: relative;

	.ref__btn, .button {
		position: absolute;
		right: 0;
		top: 0; }

	.ref__btn {
		font-size: 18px;
		color: $grey-text;
		top: 16px;
		right: 70px; }

	.button {
		height: 56px;
		width: 56px;
		line-height: 56; }


	#search__field {
		padding-left: 63px; }

	&::before {
		position: absolute;
		top: 13.5px;
		left: 20px;
		font-size: 20px;
		z-index: 1;
		content: "\f002";
		height: 28px;
		font-weight: 900;
		color: #000;
		font-family: "Font Awesome 5 Free"; } }

#ask__form {
	.button.square {
		text-align: right;
		.fas {
			font-size: 16px;
			margin-left: 93px; } } }





